.work {
  overflow-x: hidden; }

/* Hackey-type CSS as a jQuery Alternative for when the browser does not have jQuery enabled. */
.noscript {
  display: none; }

@each $proj in $projects {
  ##{$proj}:checked ~ .work-belt .work-wrap .work-container noscript #content-#{$proj} {
    display: block; } }

.return {
  display: none; }

.trigger {
  display: none;

  &:checked ~ .work-belt {
    left: -100%;

    .work-wrap .work-container {
      display: block; } } }
/* End of gross / hackey CSS */

.work-belt {
  width: 200%;
  position: relative;
  left: 0%;
  @include transition(all, 800ms);

  &.slided {
    @include transform(translate3d(-50%, 0, 0)); } }

.thumb-wrap, .work-wrap {
  width: 50%;
  float: left; }

.thumb-container {
  display: block;
  max-width: 960px;
  margin: 0px auto;
  @include clearfix;

  label {
    position: relative;
    display: block;
    float: left;
    width: 25%;
    cursor: pointer;

    .thumb-unit {
      display: block;
      overflow: hidden;

      img {
        display: block;
        max-width: 100%; } }

    @media screen and (max-width: 760px) {
      width: 50%; }

    .thumb-overlay {
      @include position(absolute);
      background: fade-out($accent, 0.1);
      @extend %animationstart_#{$animation};

      strong {
        @include position(absolute, 50% null null 50%);
        @include transform(translate(-50%, -50%));
        display: block;
        padding: 0 9%;
        color: white;
        font: {
          weight: 300;
          size: 24px; }
        text-align: center;
        text-transform: uppercase;
        line-height: 1.2; } }

    &:hover {
      z-index: 2;

      .thumb-overlay {
        @extend %#{$animation}; } } } }

.work-container {
  max-width: 600px;
  margin: 0px auto;
  padding: 0 15px;
  @include box-sizing(border-box);

  position: relative;
  display: none;

  h4, noscript h4 {
    color: $accent;
    font-weight: 300;
    text-transform: uppercase;
    font-size: 28px;
    line-height: 1.2;
    margin: 0 0 20px; }

  img {
    max-width: 100%; }

  .work-return {
    @include size(40px);
    cursor: pointer;
    opacity: 0.5;
    @include position(absolute, -4px null null -60px);

    svg {
      max-width: 100%;
      max-height: 100%;
      fill: rgba(0,0,0,0.4); }

    &:hover {
      opacity: 1; } }

  .work-btn {
    display: block;
    width: 150px;
    margin: 40px auto 0 auto;
    padding: 10px;
    color: white;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    border: 3px solid $accent;
    background: $accent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:hover {
      background: $accent-light; } } }

@media (max-width: 740px) {
  .work-container {

    .work-return {
      left: 0px; }

    h4 {
      margin-left: 60px; } } }
