.contact {
  text-align: center;

  &__socials {
    @include display(flex);
    @include justify-content(space-around);
    @include flex-wrap(wrap);
    max-width: 700px;
    margin: 0 auto;

    a {
      position: relative;
      display: block;
      @include size(50px);
      margin: 20px;
      color: $accent;
      font-size: 28px;
      line-height: 50px;
      border: 2px solid $accent;
      border-radius: 50%;

      &:after {
        content: '';
        @include position(absolute, 0px 0px 0px 0px);
        display: block;
        margin: -2px;
        border: 2px solid $accent;
        border-radius: 50%;
        opacity: 1;
        @include transition(); }

      &:hover:after {
        @include transform(scale(1.5));
        opacity: 0; } } }

  &__splitter {
    width: 250px;
    height: 0px;
    margin: 50px auto;
    border-top: 4px solid $accent; }

  &__mail {
    display: block;
    font-size: 72px;
    font-weight: 300;
    line-height: 1; } }
